.container{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background: aqua;
}
.left-col{
    flex-basis: 50%;
    margin-top:40px;
}
.left-col img{
    width: 100%;
    border-radius: 15px;
}
.right-col{
    flex-basis: 50%;
    text-align: center;
    margin-top: 100px;
}
.right-col h1{
    font-size: 40px;
    margin-top: 150px;
}
.right-col h1 span{
    color: #0188DF;
}
.right-col h3{
    font-size: 30px;
    margin-top: 20px;
}
.button{
    height:56px;
    width: 240px;
    background:#0188DF;
    color:#fff;
    font-size: 27px;
    text-transform: capitalize;
    border-radius: 8px;
    cursor: pointer;
    position: relative;
    z-index: 0;
    overflow: hidden;
    margin:16px 0;
}
.button::before{
    content: '';
    position: absolute;
    top: -100%; left: 0;
    height:100%;
    width: 100%;
    background-color: #354046;
    z-index: -1;
    transition: .2s linear;
  }
  .button:hover:before{
    top: 0%;
  }
  
  .button:hover{
    box-shadow: 1.6px 8px #0188DF
                0 4.8px 8px rgba(0,0,0,.3);
  }
