.container{
    display: flex;
    width: 100%;
    height: 100vh;
}
.left-colu{
    flex-basis: 25%;
    background: #0188DF;
    height: 30vh;
    padding: 20px;
    margin: 150px 100px;
}
.left-colu h3{
    text-align: center;
}


.center-col{
    flex-basis: 25%;
    text-align: center;
    margin: 100px 0px;
}
.img:hover{
    box-shadow: 1.6px 8px #0188DF
                0 4.8px 8px rgba(0,0,0,.3);
                transform: translate(-50px,-50px);
}
.right-colu{
    flex-basis: 32%;
    padding: 20px;
    text-align: center;
}
.right-colu form{
    flex:1 1 45rem;
    background:  #0188DF;
    border:.2rem solid#16a085;
    box-shadow:.5rem .5rem 0 rgba(22, 160, 133, .2);
    text-align: center;
    padding: 2rem;
    border-radius: .5rem;
}

.right-colu form h3{
    color:#444;
    padding-bottom: 1rem;
    font-size: 3rem;
}

.right-colu form .box{
    width: 100%;
    margin:.7rem 0;
    border-radius: .5rem;
    border:.2rem solid#16a085;
    font-size: 1.6rem;
    color: #444;
    text-transform: none;
    padding: 1rem;
}

.right-colu form .btn{
    padding:1rem 4rem;
}