.about .row{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap:2rem;
}
.about .heading{
    color: #282c34;
    margin-left: 270px;
}

.about .row .image{
    flex:1 1 45rem;
}

.about .row .image img{
    width: 100%;
}

.about .row .content{
    flex:1 1 45rem;
}

.about .row .content h3{
    /* text-shadow: var(--text-shadow); */
    font-size: 4rem;
    line-height: 1.8;
}

.about .row .content p{
    /* color: var(--light-color); */
    padding:1rem 0;
    font-size: 1.5rem;
    line-height: 1.8;
}